import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import styles from './ServiceAreas.module.css';

const ServiceAreas = () => {
  const [visibleSections, setVisibleSections] = useState(new Set());
  const [activeArea, setActiveArea] = useState(null);

  useEffect(() => {
    const observerOptions = {
      threshold: 0.1,
      rootMargin: '0px 0px -50px 0px'
    };

    const observer = new IntersectionObserver((entries) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          setVisibleSections(prev => new Set([...prev, entry.target.id]));
        }
      });
    }, observerOptions);

    document.querySelectorAll('[data-animate]').forEach(el => {
      observer.observe(el);
    });

    return () => observer.disconnect();
  }, []);

  // stanbul'daki ana hizmet blgeleri
  const serviceAreas = [
    {
      id: 'esenler',
      district: 'Esenler',
      isMainCenter: true,
      icon: '',
      gradient: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
      coverageTime: '30 dk',
      description: 'Ana merkez konumumuz. En hzl hizmet blgesi.',
      neighborhoods: [
        'Atalan', 'iftehavuzlar', 'Fevzi akmak', 'Havaalan', 
        'Kemer', 'Menderes', 'Namk Kemal', 'Nine Hatun', 
        'Orureis', 'Tuna', 'Turgutreis', 'Yavuz Selim'
      ],
      stats: {
        customers: '2500+',
        deliveryTime: 'Ayn Gn',
        rating: 4.9
      }
    },
    {
      id: 'gungoren',
      district: 'Gngren',
      icon: '',
      gradient: 'linear-gradient(135deg, #f093fb 0%, #f5576c 100%)',
      coverageTime: '45 dk',
      description: 'Youn ticari blge, zel paketler mevcut.',
      neighborhoods: [
        'Abdurrahman Nafiz Grman', 'Akaburgaz', 'Geit',
        'Gnetepe', 'Haznedar', 'Mareal Fevzi akmak',
        'Mehmet Nesih zmen', 'Merkez', 'Tozkoparan'
      ],
      stats: {
        customers: '1800+',
        deliveryTime: '48 Saat',
        rating: 4.8
      }
    },
    {
      id: 'bagcilar',
      district: 'Baclar',
      icon: '',
      gradient: 'linear-gradient(135deg, #4facfe 0%, #00f2fe 100%)',
      coverageTime: '1 saat',
      description: 'Byk yerleim blgesi, toplu indirimler.',
      neighborhoods: [
        'Balar', 'Demirkap', 'Evren', 'Fatih', 'Gztepe',
        'Gneli', 'nn', 'Kemalpaa', 'Kirazl', 'Mahmutbey',
        'Merkez', 'Sakzaac', 'Sancaktepe', 'Yldztepe', 'Yzyl'
      ],
      stats: {
        customers: '3200+',
        deliveryTime: '48 Saat',
        rating: 4.9
      }
    },
    {
      id: 'bayramapasa',
      district: 'Bayrampaa',
      icon: '',
      gradient: 'linear-gradient(135deg, #10b981 0%, #059669 100%)',
      coverageTime: '45 dk',
      description: 'Merkezi konum, hzl eriim imkan.',
      neighborhoods: [
        'Altntepsi', 'Cevatpaa', 'Kartaltepe', 'Muratpaa',
        'Ortamahalle', 'Ruhupaa', 'Terazidere', 'Vatan', 'Yldrm'
      ],
      stats: {
        customers: '1500+',
        deliveryTime: '24 Saat',
        rating: 4.7
      }
    },
    {
      id: 'zeytinburnu',
      district: 'Zeytinburnu',
      icon: '',
      gradient: 'linear-gradient(135deg, #f59e0b 0%, #ef4444 100%)',
      coverageTime: '1 saat',
      description: 'Sahil blgesi, zel temizlik zmleri.',
      neighborhoods: [
        'Betelsiz', 'rpc', 'Gkalp', 'Merkezefendi',
        'Nuripaa', 'Smer', 'Telsiz', 'Veliefendi', 'Yenidoan'
      ],
      stats: {
        customers: '1200+',
        deliveryTime: '48 Saat',
        rating: 4.8
      }
    },
    {
      id: 'bahcelievler',
      district: 'Bahelievler',
      icon: '',
      gradient: 'linear-gradient(135deg, #8b5cf6 0%, #ec4899 100%)',
      coverageTime: '1 saat',
      description: 'Premium yerleim, lks hizmet paketleri.',
      neighborhoods: [
        'Adnan Kahveci', 'Bahelievler', 'Cumhuriyet', 'Erturulgazi',
        'Fevzi akmak', 'Hrriyet', 'Kocasinan', 'Sirinevler',
        'Soanl', 'irinevler', 'Yenibosna', 'Zafer'
      ],
      stats: {
        customers: '2100+',
        deliveryTime: '48 Saat',
        rating: 4.9
      }
    }
  ];

  const isVisible = (sectionId) => visibleSections.has(sectionId);

  return (
    <div className={styles.serviceAreas}>
      {/* Hero Section */}
      <section className={styles.hero} id="hero" data-animate>
        <div className={styles.heroBackground}>
          <div className={styles.heroShapes}>
            <div className={styles.shape1}></div>
            <div className={styles.shape2}></div>
            <div className={styles.shape3}></div>
          </div>
        </div>
        
        <div className="container">
          <div className={`${styles.heroContent} ${isVisible('hero') ? 'fade-in visible' : 'fade-in'}`}>
            <span className={styles.heroLabel}> Hizmet Blgeleri</span>
            <h1 className={styles.heroTitle}>
              <span className={styles.titleMain}>stanbul'un Her Kesinde</span>
              <span className={styles.titleSub}>Gvenilir Hal Ykama Hizmeti</span>
            </h1>
            <p className={styles.heroDescription}>
              Esenler merkezli olarak 6 ilede 60+ mahallede cretsiz alma-teslim ile 
              profesyonel hal ykama hizmeti sunuyoruz.
            </p>
            
            <div className={styles.heroStats}>
              <div className={styles.statItem}>
                <span className={styles.statNumber}>60+</span>
                <span className={styles.statLabel}>Mahalle</span>
              </div>
              <div className={styles.statItem}>
                <span className={styles.statNumber}>0</span>
                <span className={styles.statLabel}>Tama creti</span>
              </div>
              <div className={styles.statItem}>
                <span className={styles.statNumber}>15k+</span>
                <span className={styles.statLabel}>Mutlu Mteri</span>
              </div>
            </div>
            
            <div className={styles.heroActions}>
              <Link to="/contact" className={styles.primaryAction}>
                <span>Blgeni Kontrol Et</span>
                <i className="fas fa-map-marker-alt"></i>
              </Link>
              <a href="tel:+905001234567" className={styles.secondaryAction}>
                <i className="fas fa-phone"></i>
                <span>Hemen Ara</span>
              </a>
            </div>
          </div>
        </div>
      </section>

      {/* Service Areas Grid */}
      <section className={styles.areasSection} id="areas" data-animate>
        <div className="container">
          <div className={`${styles.sectionHeader} ${isVisible('areas') ? 'fade-in visible' : 'fade-in'}`}>
            <span className={styles.sectionLabel}> Hizmet Haritas</span>
            <h2 className={styles.sectionTitle}>
              Kapsaml Hizmet Amz
            </h2>
            <p className={styles.sectionDescription}>
              Her blge iin zel zmler ve hzl teslimat seenekleri
            </p>
          </div>
          
          <div className={`${styles.areasGrid} ${isVisible('areas') ? 'fade-in visible' : 'fade-in'}`}>
            {serviceAreas.map((area, index) => (
              <div 
                key={area.id} 
                className={`${styles.areaCard} ${area.isMainCenter ? styles.mainCenter : ''} ${activeArea === area.id ? styles.active : ''}`}
                style={{ animationDelay: `${index * 0.1}s` }}
                onMouseEnter={() => setActiveArea(area.id)}
                onMouseLeave={() => setActiveArea(null)}
              >
                <div className={styles.cardHeader} style={{ background: area.gradient }}>
                  <div className={styles.areaInfo}>
                    <div className={styles.areaIcon}>{area.icon}</div>
                    <div className={styles.areaDetails}>
                      <h3 className={styles.areaName}>{area.district}</h3>
                      <p className={styles.areaDescription}>{area.description}</p>
                    </div>
                  </div>
                  {area.isMainCenter && (
                    <div className={styles.mainBadge}>
                      <span>Ana Merkez</span>
                    </div>
                  )}
                </div>
                
                <div className={styles.cardBody}>
                  <div className={styles.areaStats}>
                    <div className={styles.statBox}>
                      <span className={styles.statValue}>{area.stats.customers}</span>
                      <span className={styles.statDesc}>Mteri</span>
                    </div>
                    <div className={styles.statBox}>
                      <span className={styles.statValue}>{area.coverageTime}</span>
                      <span className={styles.statDesc}>Ulam</span>
                    </div>
                    <div className={styles.statBox}>
                      <span className={styles.statValue}>{area.stats.rating}</span>
                      <span className={styles.statDesc}>Puan</span>
                    </div>
                  </div>
                  
                  <div className={styles.neighborhoodsContainer}>
                    <h4 className={styles.neighborhoodsTitle}>
                      <span>Hizmet Verilen Mahalleler</span>
                      <span className={styles.neighborhoodCount}>{area.neighborhoods.length}</span>
                    </h4>
                    <div className={styles.neighborhoodsList}>
                      {area.neighborhoods.slice(0, 8).map((neighborhood, idx) => (
                        <span key={idx} className={styles.neighborhood}>
                          {neighborhood}
                        </span>
                      ))}
                      {area.neighborhoods.length > 8 && (
                        <span className={styles.moreNeighborhoods}>
                          +{area.neighborhoods.length - 8} daha
                        </span>
                      )}
                    </div>
                  </div>
                </div>
                
                <div className={styles.cardFooter}>
                  <div className={styles.deliveryInfo}>
                    <div className={styles.deliveryIcon}>
                      <i className="fas fa-truck"></i>
                    </div>
                    <div className={styles.deliveryDetails}>
                      <span className={styles.deliveryTitle}>Teslimat Sresi</span>
                      <span className={styles.deliveryTime}>{area.stats.deliveryTime}</span>
                    </div>
                  </div>
                  <Link to="/contact" className={styles.orderButton}>
                    <span>Sipari Ver</span>
                    <i className="fas fa-arrow-right"></i>
                  </Link>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className={styles.ctaSection} id="cta" data-animate>
        <div className="container">
          <div className={`${styles.ctaContent} ${isVisible('cta') ? 'fade-in visible' : 'fade-in'}`}>
            <div className={styles.ctaText}>
              <h2>Blgeniz Listede Yok mu?</h2>
              <p>
                Henz listelenmemi blgeler iin zel zmler retiyoruz. 
                letiime gein, size en yakn hizmet noktasn bulalm.
              </p>
            </div>
            <div className={styles.ctaActions}>
              <Link to="/contact" className={styles.ctaPrimary}>
                <span>zel Teklif Al</span>
                <i className="fas fa-map-marked-alt"></i>
              </Link>
              <a href="https://wa.me/905001234567" className={styles.ctaSecondary}>
                <i className="fab fa-whatsapp"></i>
                <span>WhatsApp</span>
              </a>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default ServiceAreas;
