import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import styles from './Prices.module.css';

const Prices = () => {
  const [visibleSections, setVisibleSections] = useState(new Set());
  const [activeTab, setActiveTab] = useState('residential');

  useEffect(() => {
    const observerOptions = {
      threshold: 0.1,
      rootMargin: '0px 0px -50px 0px'
    };

    const observer = new IntersectionObserver((entries) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          setVisibleSections(prev => new Set([...prev, entry.target.id]));
        }
      });
    }, observerOptions);

    // Re-observe all elements when activeTab changes
    const observeElements = () => {
      document.querySelectorAll('[data-animate]').forEach(el => {
        observer.observe(el);
      });
    };

    // Initial observation
    observeElements();
    
    // Re-observe after a short delay to catch dynamically added elements
    const timeoutId = setTimeout(observeElements, 100);

    return () => {
      observer.disconnect();
      clearTimeout(timeoutId);
    };
  }, [activeTab]); // Add activeTab to dependencies

  // Ev kullanclar iin detayl fiyat listesi
  const residentialServices = [
    {
      category: 'Hal Temizlii',
      icon: '',
      color: '#667eea',
      items: [
        { name: 'Makina Hals', price: 25, unit: 'm', description: 'Standard makina dokumas hallar', popular: true },
        { name: 'El Dokumas Hal', price: 45, unit: 'm', description: 'El dokumas ve antika hallar' },
        { name: 'Yn Hal', price: 35, unit: 'm', description: 'Doal yn elyafl hallar' },
        { name: 'Shaggy Hal', price: 30, unit: 'm', description: 'Uzun tyl modern hallar' },
        { name: 'Kilim & Dz Dokuma', price: 20, unit: 'm', description: 'nce dokuma kilim ve yer minderleri' }
      ]
    },
    {
      category: 'Koltuk & Mobilya',
      icon: '',
      color: '#f093fb',
      items: [
        { name: 'Tekli Koltuk', price: 150, unit: 'adet', description: 'Berjer ve tekli koltuklar', popular: true },
        { name: 'kili Koltuk', price: 250, unit: 'adet', description: 'ki kiilik koltuklar' },
        { name: 'l Koltuk', price: 350, unit: 'adet', description: ' kiilik koltuklar' },
        { name: 'Ke Takm', price: 800, unit: 'takm', description: 'L eklinde ke takmlar' },
        { name: 'Yemek Sandalyesi', price: 35, unit: 'adet', description: 'Kuma kapl yemek sandalyeleri' }
      ]
    },
    {
      category: 'Yatak & Uyku Sistemleri',
      icon: '',
      color: '#4facfe',
      items: [
        { name: 'Tek Kiilik Yatak', price: 150, unit: 'adet', description: '90x190 tek kiilik yatak', popular: true },
        { name: 'ift Kiilik Yatak', price: 200, unit: 'adet', description: '160x200 ift kiilik yatak' },
        { name: 'King Size Yatak', price: 250, unit: 'adet', description: '180x200 king size yatak' },
        { name: 'Yastk Temizlii', price: 25, unit: 'adet', description: 'Tm yastk eitleri' },
        { name: 'Yorgan Temizlii', price: 75, unit: 'adet', description: 'Yorgan ve nevresim takmlar' }
      ]
    },
    {
      category: 'Perde & Tekstil',
      icon: '',
      color: '#10b981',
      items: [
        { name: 'Stor Perde', price: 15, unit: 'm', description: 'Zebra ve rollo stor perdeler' },
        { name: 'Klasik Perde', price: 20, unit: 'm', description: 'Fon ve tl perdeler' },
        { name: 'Blackout Perde', price: 25, unit: 'm', description: 'Kaln karartma perdeleri' },
        { name: 'Jaluzi Temizlii', price: 12, unit: 'm', description: 'Ahap ve PVC jaluziler' }
      ]
    },
    {
      category: 'zel Hizmetler',
      icon: '',
      color: '#f59e0b',
      items: [
        { name: 'Leke karma', price: 50, unit: 'ilem', description: 'zel leke karma ilemi' },
        { name: 'Koku Giderme', price: 40, unit: 'ilem', description: 'Koku giderme ve deodorize' },
        { name: 'Antibakteriyel Uygulama', price: 30, unit: 'ilem', description: 'Hijyen ve dezenfeksiyon' },
        { name: 'Express Teslimat', price: 100, unit: 'hizmet', description: '24 saat iinde teslimat' }
      ]
    }
  ];

  // letmeler iin paketler
  const commercialPackages = [
    {
      id: 'restaurant-chair',
      name: 'Restoran Sandalye Paketi',
      popular: true,
      price: 1899,
      originalPrice: 2299,
      unit: 'paket',
      description: 'Restoran ve kafe iletmeleri iin sandalye temizlik paketi',
      gradient: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
      features: [
        '50 Adet Sandalye Temizlii',
        'Leke ve Kir Giderme',
        'Antibakteriyel Uygulama',
        'Hzl Kurutma Sistemi',
        'alma Saatleri Dnda Hizmet',
        'Hijyen Sertifikas',
        'Aylk Bakm Seenei',
        'cretsiz Keif'
      ]
    },
    {
      id: 'office-carpet-chair',
      name: 'Ofis Hal & Mobilya Paketi',
      popular: false,
      price: 2499,
      originalPrice: 2999,
      unit: 'paket',
      description: 'Ofisler iin komple hal ve mobilya temizlik zm',
      gradient: 'linear-gradient(135deg, #f093fb 0%, #f5576c 100%)',
      features: [
        'Ofis Hals (100m ye kadar)',
        'Ofis Sandalyeleri (30 adet)',
        'Meeting Room Temizlii',
        'Koltuk ve Mobilya Temizlii',
        'Antibakteriyel Dezenfeksiyon',
        'UV Sterilizasyon',
        'Mesai Saatleri Dnda Hizmet',
        'eyreklik Bakm Plan'
      ]
    },
    {
      id: 'hotel-complete',
      name: 'Otel Komple Temizlik',
      popular: false,
      price: 4999,
      originalPrice: 5999,
      unit: 'paket',
      description: 'Otel ve konaklama iletmeleri iin kapsaml temizlik',
      gradient: 'linear-gradient(135deg, #4facfe 0%, #00f2fe 100%)',
      features: [
        'Tm Oda Hallar',
        'Lob ve Ortak Alan Hallar',
        'Koltuk ve Sandalye Temizlii',
        'Perde ve Stor Temizlii',
        'Yatak ve Yastk Hijyeni',
        'Premium UV Sterilizasyon',
        '24/7 Acil Mdahale Hatt',
        'Mteri Memnuniyet Garantisi'
      ]
    },
    {
      id: 'clinic-hygiene',
      name: 'Klinik & Salk Merkezi',
      popular: false,
      price: 3299,
      originalPrice: 3799,
      unit: 'paket',
      description: 'Salk kurulular iin zel hijyen paketi',
      gradient: 'linear-gradient(135deg, #10b981 0%, #059669 100%)',
      features: [
        'Medikal Grade Temizlik',
        'Hasta Sandalyesi Hijyeni',
        'Bekleme Salonu Temizlii',
        'Antibakteriyel & Antiviral',
        'Hastane Standart Sterilizasyon',
        'zel Hijyen Protokol',
        'Sertifikal Temizlik Ekibi',
        'Haftalk Bakm Seenei'
      ]
    }
  ];

  const isVisible = (sectionId) => visibleSections.has(sectionId);

  return (
    <div className={styles.prices}>
      {/* Hero Section */}
      <section className={styles.hero} id="hero" data-animate>
        <div className={styles.heroBackground}>
          <div className={styles.heroShapes}>
            <div className={styles.shape1}></div>
            <div className={styles.shape2}></div>
            <div className={styles.shape3}></div>
          </div>
        </div>
        
        <div className="container">
          <div className={`${styles.heroContent} ${isVisible('hero') ? 'fade-in visible' : 'fade-in'}`}>
            <span className={styles.heroLabel}> effaf Fiyatlandrma</span>
            <h1 className={styles.heroTitle}>
              <span className={styles.titleMain}>Diba Hal Ykama</span>
              <span className={styles.titleSub}>Fiyat Listesi 2025</span>
            </h1>
            <p className={styles.heroDescription}>
              effaf fiyatlarmz ile kaliteli hizmeti uygun maliyetle aln. 
              Gizli cret yok, srpriz fatura yok!
            </p>
            
            <div className={styles.heroStats}>
              <div className={styles.statItem}>
                <span className={styles.statNumber}>%30</span>
                <span className={styles.statLabel}>Tasarruf</span>
              </div>
              <div className={styles.statItem}>
                <span className={styles.statNumber}>0</span>
                <span className={styles.statLabel}>Ek cret</span>
              </div>
              <div className={styles.statItem}>
                <span className={styles.statNumber}>24sa</span>
                <span className={styles.statLabel}>Teslimat</span>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Package Selection Tabs */}
      <section className={styles.tabsSection} id="tabs" data-animate>
        <div className="container">
          <div className={`${styles.tabsContainer} ${isVisible('tabs') ? 'fade-in visible' : 'fade-in'}`}>
            <div className={styles.tabButtons}>
              <button 
                className={`${styles.tabButton} ${activeTab === 'residential' ? styles.active : ''}`}
                onClick={() => setActiveTab('residential')}
              >
                <span className={styles.tabIcon}></span>
                <span>Ev Kullanclar</span>
              </button>
              <button 
                className={`${styles.tabButton} ${activeTab === 'commercial' ? styles.active : ''}`}
                onClick={() => setActiveTab('commercial')}
              >
                <span className={styles.tabIcon}></span>
                <span>letmeler</span>
              </button>
            </div>
          </div>
        </div>
      </section>

      {/* Content Based on Active Tab */}
      {activeTab === 'residential' ? (
        // Ev kullanclar iin tekli fiyatlar
        <section className={styles.servicesSection} id="services" data-animate>
          <div className="container">
            <div className={`${styles.sectionHeader} ${isVisible('services') || activeTab === 'residential' ? 'fade-in visible' : 'fade-in'}`}>
              <span className={styles.sectionLabel}> Ev Kullanclar</span>
              <h2 className={styles.sectionTitle}>
                Birim Fiyatlarmzla htiyacnz Kadar Hizmet Aln
              </h2>
              <p className={styles.sectionDescription}>
                Sadece ihtiyacnz olan hizmetler iin deme yapn. Paket zorunluluu yok!
              </p>
            </div>
            
            <div className={`${styles.servicesContainer} ${isVisible('services') || activeTab === 'residential' ? 'fade-in visible' : 'fade-in'}`}>
              {residentialServices.map((category, index) => (
                <div 
                  key={category.category} 
                  className={styles.serviceCategory}
                  style={{ animationDelay: `${index * 0.2}s` }}
                >
                  <div className={styles.categoryHeader}>
                    <div className={styles.categoryIcon} style={{ color: category.color }}>
                      {category.icon}
                    </div>
                    <h3 className={styles.categoryTitle}>{category.category}</h3>
                  </div>
                  
                  <div className={styles.categoryServices}>
                    {category.items.map((item, idx) => (
                      <div key={idx} className={`${styles.serviceItem} ${item.popular ? styles.popularService : ''}`}>
                        <div className={styles.serviceInfo}>
                          <h4 className={styles.serviceName}>
                            {item.name}
                            {item.popular && <span className={styles.popularTag}>Popler</span>}
                          </h4>
                          <p className={styles.serviceDescription}>{item.description}</p>
                        </div>
                        <div className={styles.servicePrice}>
                          <span className={styles.price}>{item.price}</span>
                          <span className={styles.unit}>/{item.unit}</span>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              ))}
            </div>
          </div>
        </section>
      ) : (
        // letmeler iin paketler
        <section className={styles.packagesSection} id="packages" data-animate>
          <div className="container">
            <div className={`${styles.sectionHeader} ${isVisible('packages') || activeTab === 'commercial' ? 'fade-in visible' : 'fade-in'}`}>
              <span className={styles.sectionLabel}> letmeler</span>
              <h2 className={styles.sectionTitle}>
                 Yerinize zel Hazrlanm Paket zmler
              </h2>
            </div>
            
            <div className={`${styles.packagesContainer} ${isVisible('packages') || activeTab === 'commercial' ? 'fade-in visible' : 'fade-in'}`}>
              {commercialPackages.map((pkg, index) => (
                <div 
                  key={pkg.id} 
                  className={`${styles.packageCard} ${pkg.popular ? styles.popular : ''}`}
                  style={{ animationDelay: `${index * 0.1}s` }}
                >
                  {pkg.popular && (
                    <div className={styles.popularBadge}>
                      <span>En Popler</span>
                    </div>
                  )}
                  
                  <div className={styles.packageHeader} style={{ background: pkg.gradient }}>
                    <h3 className={styles.packageName}>{pkg.name}</h3>
                    <div className={styles.packagePrice}>
                      <span className={styles.price}>{pkg.price}</span>
                      {pkg.originalPrice && (
                        <span className={styles.originalPrice}>{pkg.originalPrice}</span>
                      )}
                      <span className={styles.unit}>/{pkg.unit}</span>
                    </div>
                    <p className={styles.packageDescription}>{pkg.description}</p>
                  </div>
                  
                  <div className={styles.packageBody}>
                    <div className={styles.packageFeatures}>
                      {pkg.features.map((feature, idx) => (
                        <div key={idx} className={styles.feature}>
                          <i className="fas fa-check"></i>
                          <span>{feature}</span>
                        </div>
                      ))}
                    </div>
                    
                    <Link to="/contact" className={styles.packageAction}>
                      <span>Bu Paketi Se</span>
                      <i className="fas fa-arrow-right"></i>
                    </Link>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </section>
      )}

      {/* CTA Section */}
      <section className={styles.ctaSection} id="cta" data-animate>
        <div className="container">
          <div className={`${styles.ctaContent} ${isVisible('cta') ? 'fade-in visible' : 'fade-in'}`}>
            <div className={styles.ctaText}>
              <h2>zel Teklifinizi Aln</h2>
              <p>
                Daha byk projeler iin zel fiyat teklifleri hazrlyoruz. 
                cretsiz keif ve detayl fiyat teklifi iin iletiime gein.
              </p>
            </div>
            <div className={styles.ctaActions}>
              <Link to="/contact" className={styles.ctaPrimary}>
                <span>Teklif Al</span>
                <i className="fas fa-calculator"></i>
              </Link>
              <a href="tel:+905001234567" className={styles.ctaSecondary}>
                <i className="fas fa-phone"></i>
                <span>Hemen Ara</span>
              </a>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Prices;
